import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { importMemo } from '@ali/4ever-factory';

export default function ImportMemoMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;
  const { value } = controller;
  const isDisabled = controller.query('isSelectionInImage') || controller.query('isSelectionInLinkCard');

  const handleClick = () => {
    if (isDisabled) {
      return;
    }
    controller.run('onAction', importMemo());
  };

  return (
    <MenuItem {...rest} onClick={handleClick} disabled={isDisabled}>
      {title}
    </MenuItem>
  );
}
